                                            backgroundColor: '#ea0b00',
                                            borderColor: '#ea0b00'
                                        },
                                        on: {
                                            click: () => {
                                                let v = [];
                                                v.push(params.row);

                                                this.deleteBox(v);


                                            }
                                        }
                                    }, '删除')
                                ]);


                            }
                            if (stateNums == 4) { //投放中状态，右边显示修改，删除   上面按钮显示暂停，删除
                                return h('div', [

                                    h('Button', {
                                        props: {
                                            type: 'error',
                                            size: 'small',
                                            // disabled:this.disabled2
                                        },
                                        style: {
                                            marginRight: '5px',
                                            backgroundColor: '#1fa2fd',
                                            borderColor: '#1fa2fd'
                                        },
                                        on: {
                                            click: () => {
                                                if (typeNum == 0) { //非原生
                                                    this.$refs.unAdNativeUpdate.beforeLoad(deliverId, 2);

                                                }
                                                if (typeNum == 1) { //移动原生
                                                    this.$refs.adNativeUpdate.beforeLoad(deliverId, 2);

                                                }

                                            }
                                        }
                                    }, '查看'),
                                    h('Button', {
                                        props: {
                                            type: 'info',
                                            size: 'small'
                                        },
                                        style: {
                                            marginRight: '5px',
                                            backgroundColor: '#0676c4',
                                            borderColor: '#0676c4'
                                        },
                                        on: {
                                            click: () => {
                                                if (typeNum == 0) { //非原生
                                                    this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                                                }
                                                if (typeNum == 1) { //移动原生
                                                    this.$refs.adNativeUpdate.beforeLoad(deliverId);

                                                }

                                            }
                                        }
                                    }, '修改'), h('Button', {
                                        props: {
                                            type: 'error',
                                            size: 'small',
                                            // disabled:this.disabled2
                                        },
                                        style: {

                                            backgroundColor: '#ea0b00',
                                            borderColor: '#ea0b00'
                                        },
                                        on: {
                                            click: () => {
                                                //alert("sdsasa")
                                                let v = [];
                                                v.push(params.row);

                                                this.deleteBox(v);

                                            }
                                        }
                                    }, '删除')
                                ]);


                            }
                            if (stateNums == 5) { //投放完成状态，右边显示修改，删除   上面按钮显示删除
                                return h('div', [

                                    h('Button', {
                                        props: {
                                            type: 'error',
                                            size: 'small',
                                            // disabled:this.disabled2
                                        },
                                        style: {
                                            marginRight: '5px',
                                            backgroundColor: '#1fa2fd',
                                            borderColor: '#1fa2fd'
                                        },
                                        on: {
                                            click: () => {
                                                if (typeNum == 0) { //非原生
                                                    this.$refs.unAdNativeUpdate.beforeLoad(deliverId, 2);

                                                }
                                                if (typeNum == 1) { //移动原生
                                                    this.$refs.adNativeUpdate.beforeLoad(deliverId, 2);

                                                }

                                            }
                                        }
                                    }, '查看'),

                                    h('Button', {
                                        props: {
                                            type: 'info',
                                            size: 'small',

                                        },
                                        style: {
                                            marginRight: '5px',
                                            backgroundColor: '#0676c4',
                                            borderColor: '#0676c4'
                                        },
                                        on: {
                                            click: () => {
                                                if (typeNum == 0) { //非原生
                                                    this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                                                }
                                                if (typeNum == 1) { //移动原生
                                                    this.$refs.adNativeUpdate.beforeLoad(deliverId);

                                                }

                                            }
                                        }
                                    }, '修改'), h('Button', {
                                        props: {
                                            type: 'error',
                                            size: 'small',
                                            // disabled:this.disabled2
                                        },
                                        style: {

                                            backgroundColor: '#ea0b00',
                                            borderColor: '#ea0b00'
                                        },
                                        on: {
                                            click: () => {
                                                let v = [];
                                                v.push(params.row);

                                                this.deleteBox(v);

                                            }
                                        }
                                    }, '删除')
                                ]);


                            }

                        }


                    }

                ],
            }
        },
        created() {

            //   if(this.$route.query.flag ==1){
            //     this.isShow =true;
            //   this.orderId = this.$route.query.id;
            // }else{
            //   this.isShow =false;
            //   this.orderId = '';
            // }
            //  this.initList();
            //  this.initStatus ();
            //  this.getIsNativeMap ();
            //  this.getCodeGroup();
            //  this.getOrderListFun();
            //  this.getCodeListFun();
            //  this.getMaterialTemplateListFun();


        },

        mounted() {
            this.getOrderListFun();
            this.initStatus();
            if (this.$route.query.flag == 1) {
                this.flag = 1
                this.orderId = this.$route.query.id
                this.orderName = this.$route.query.orderName
            } else if (this.$route.query.flag == 2) {
                this.flag = 2
                this.orderId = this.$route.query.id
                this.orderName = this.$route.query.orderName
                    //this.modal1.push(this.$route.query.deliverStatus)
                this.status = this.$route.query.deliverStatus
            }
            //this.initList();
            this.getEveryDeliverStatusCountById();
            this.getIsNativeMap();
            this.getCodeGroup();
            this.getCodeListFun();
            this.getMaterialTemplateListFun();


        },

        methods: {


            selectData: function(selection) {
                //删除状态，右边只显示修改  上面按钮一个不显示 0

                //草稿状态，右边显示修改，删除   上面按钮显示删除 1

                //暂停状态，右边显示修改，删除   上面按钮显示删除，启用 2

                //待投放状态，右边显示修改，删除   上面按钮显示暂停，删除 3

                //投放中状态，右边显示修改，删除   上面按钮显示暂停，删除 4

                //投放完成状态，右边显示修改，删除   上面按钮显示删除 5

                let canStops = selection.filter((item) => {
                    let stateNums = item.stateNums;
                    return stateNums == 3 || stateNums == 4;
                });

                let canDeletes = selection.filter((item) => {
                    let stateNums = item.stateNums;
                    return stateNums != 0;
                });

                let canAdds = selection.filter((item) => {
                    let stateNums = item.stateNums;
                    return stateNums == 2;

                });

                this.isStops = !canStops.length > 0;
                this.stopAdDeliverParams = canStops;
                this.isAdds = !canAdds.length > 0;
                this.addAdDeliverParams = canAdds;
                this.isDeletes = !canDeletes.length > 0;
                this.deleteAdDeliverParams = canDeletes;
            },

            stopAll: function() {
                this.stopBox(this.stopAdDeliverParams);
            },
            deleteAll: function() {
                this.deleteBox(this.deleteAdDeliverParams);

            },
            addAll: function() {
                this.addBox(this.addAdDeliverParams);

            },

            stopBox(params) {

                let deliverNames = params.map((item, index) => {
                    return item.name;
                });
                let deliverIds = params.map((item, index) => {
                    return item.deliverId;
                });
                this.$Modal.confirm({
                    title: '暂停广告投放',
                    content: `你确定要暂停` + deliverNames.join(","),
                    onOk: () => {

                        this.stopDeliverData(deliverIds.join(","));

                    }
                })
            },

            stopDeliverData(AdDeliverId) {
                const url = "/deliver/stopDeliver.json?deliverId=" + AdDeliverId;

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();

                    }
                })
            },


            deleteBox(params) {

                let deliverNames = params.map((item, index) => {
                    return item.name;
                });
                let deliverIds = params.map((item, index) => {
                    return item.deliverId;
                });
                this.$Modal.confirm({
                    title: '删除广告投放',
                    content: `你确定要删除` + deliverNames.join(","),
                    onOk: () => {

                        this.deleteDeliverData(deliverIds.join(","));

                    }
                })
            },

            deleteDeliverData(AdDeliverId) {


                const url = "/deliver/deleteDeliver.json?deliverId=" + AdDeliverId;

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();

                    }
                })
            },


            addBox(params) {

                let deliverNames = params.map((item, index) => {
                    return item.name;
                });
                let deliverIds = params.map((item, index) => {
                    return item.deliverId;
                });
                this.$Modal.confirm({
                    title: '启动广告投放',
                    content: `你确定要启动` + deliverNames.join(","),
                    onOk: () => {

                        this.addDeliverData(deliverIds.join(","));

                    }
                })
            },

            addDeliverData(AdDeliverId) {


                const url = "/deliver/enableDeliver.json?deliverId=" + AdDeliverId;

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();

                    }
                })
            },


            // **************************************
            radioOnChange(data) {
                this.startDate = "";
                this.endDate = "";
                this.dateSelect = []
                if (data == "设置投放时间") {
                    this.showBol = true;
                } else {

                    this.initList();
                    this.showBol = false;
                }
            },
            initList() {

                this.loading = true;
                const url = "/deliver/getDeliverList.json?nameLike=" + encodeURIComponent(this.value) + "&status=" + this.status + "&isNative=" + this.isNative + "&pageSize=" + this.pageSize + "&pageNo=" + this.pageNo + "&orderId=" + this.orderId + "&codeGroupId=" + this.codeGroupId + "&codeId=" + this.codeId + "&materialId=" + this.materialId + "&startDate=" + this.startDate + "&endDate=" + this.endDate
                this.$Http.get(url, {}).then(res => {
                    this.loading = false;
                    if (res.body.status == 0) {
                        this.adData = [];
                        this.totalCount = res.body.data.totalCount;

                        console.log(res.body.data);
                        let result = res.body.data.result;

                        for (let item of result) {
                            let isNative = "";
                            let deliverStatus = "";
                            let level = "";


                            if (item.isNative == 0) {
                                isNative = "非原生"
                            } else if (item.isNative == 1) {
                                isNative = "原生"
                            }

                            if (item.deliverStatus == 0) {
                                deliverStatus = "删除";

                                // this.disabled2 = 'false';
                            } else if (item.deliverStatus == 1) {
                                deliverStatus = "草稿"
                            } else if (item.deliverStatus == 2) {
                                deliverStatus = "暂停"
                            } else if (item.deliverStatus == 3) {
                                deliverStatus = "待投放"
                            } else if (item.deliverStatus == 4) {
                                deliverStatus = "投放中"
                            } else if (item.deliverStatus == 5) {
                                deliverStatus = "投放完成"
                            }
                            if (item.level == 1) {
                                level = "独占"
                            } else if (item.level == 2) {
                                level = "优先"
                            } else if (item.level == 3) {
                                level = "标准"
                            } else if (item.level == 4) {
                                level = "补余"
                            }
                            var startDate = item.startDate;
                            var endDate = item.endDate;
                            let percent = item.progressRate;

                            let subDate = startDate + "-" + endDate;

                            // console.log(arr[key].fxadDeliverCodeMaterialList);
                            let resultList = {
                                'name': item.deliverName,
                                'type': isNative,
                                'typeNum': item.isNative,
                                'state': deliverStatus,
                                'stateNums': item.deliverStatus,
                                'deliverCodeList': item.fxadDeliverCodeList,
                                'deliverMaterialList': item.fxadDeliverMaterialList,
                                'deliverTimeList': item.deliverTimeList,
                                'totalWeightNum': item.totalWeightNum,
                                'dateType': item.dateType,
                                'priority': level,
                                'date': subDate,
                                'schedule': percent,
                                'deliverId': item.fxAdDeliverId
                            }


                            this.adData.push(resultList);


                        }


                    }


                })
            },
            changePageNo: function(pageNo) {
                // this.pageSize = 10;
                this.pageNo = pageNo;
                this.initList();
            },
            changePageSize: function(pageSize) {
                this.pageNo = 1;
                this.pageSize = pageSize;
                this.initList();
            },
            getFilterData: function() {
                // this.pageSize = 10;
                // this.pageNo = 1;
                this.recoverFilterData();
            },
            recoverFilterData: function() {
                this.isStops = true;
                this.isAdds = true;
                this.isDeletes = true;
                this.stopAdDeliverParams = [];
                this.addAdDeliverParams = [];
                this.deleteAdDeliverParams = [];
                this.initList();
            },

            sx() {
                this.showBol2 = !this.showBol2;
                if (this.showBol2) {
                    this.sxIcon = "chevron-up"
                } else {
                    this.sxIcon = "chevron-down"
                }
            },


            initStatus() {
                const url = "/common/getDeliverStatusMap.json?";
                this.$Http.get(url, {}).then(res => {
                    let statusList = res.body.data.list;
                    this.initStatusList = statusList;


                    if (this.flag == 1) {
                        this.initStatusList.forEach((item, index) => {

                            if (item.id != 0) {

                                this.model1.push(item.id)

                            }

                        });
                    } else if (this.flag == 2) {
                        this.model1.push(this.$route.query.deliverStatus)
                    }


                })
            },
            getIsNativeMap() {
                const url = "/common/getIsNativeMap.json?";
                this.$Http.get(url, {}).then(res => {
                    this.getIsNativeMapList = res.body.data.list;
                })
            },
            getOrderListFun() {
                const url = "/order/getOrderList.json?nameLike&status&deliverStatus=&pageSize=" + this.adPageSize + "&pageNo=" + this.adpageNo;
                this.$Http.get(url, {}).then(res => {
                    for (var i = 0; i < res.body.data.result.length; i++) {
                        this.getOrderList.push(res.body.data.result[i])
                    }
                    this.totalPages = res.body.data.totalPages;
                })
            },
            getCodeListFun() {
                const url = "/code/getCodeList.json?nameLike=&status&isNative&useStatus=&pageSize=" + this.adPageSize2 + "&pageNo=" + this.adpageNo2;
                this.$Http.get(url, {}).then(res => {

                    for (var i = 0; i < res.body.data.result.length; i++) {
                        this.getCodeList.push(res.body.data.result[i])
                    }
                    this.totalPages2 = res.body.data.totalPages;
                })
            },
            getMaterialTemplateListFun() {
                const url = "/materialTemplate/getMaterialTemplateList.json?nameLike=&status";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        console.log(res);
                        for (var i = 0; i < res.body.data.result.length; i++) {
                            this.getMaterialTemplateList.push(res.body.data.result[i])
                        }
                        this.totalPages3 = res.body.data.totalPages;
                    }

                })
            },
            getEveryDeliverStatusCountById() {
                const url = "/common/getEveryDeliverStatusCountById.json?orderId=" + this.orderId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {


                        let myArray = res.body.data.list


                        myArray.forEach((item, index) => {


                            if (item.deliverStatus == 0) {
                                this.deliverStatus0 = item.totalNumber;

                                // this.disabled2 = 'false';
                            } else if (item.deliverStatus == 1) {
                                this.deliverStatus1 = item.totalNumber;

                            } else if (item.deliverStatus == 2) {
                                this.deliverStatus2 = item.totalNumber;

                            } else if (item.deliverStatus == 3) {
                                this.deliverStatus3 = item.totalNumber;

                            } else if (item.deliverStatus == 4) {
                                this.deliverStatus4 = item.totalNumber;

                            } else if (item.deliverStatus == 5) {
                                this.deliverStatus5 = item.totalNumber;
                            }


                        })


                    }

                })
            },
            loadMore() {
                if (this.adpageNo == this.totalPages) {
                    this.textLoadMore = "已到最底部"
                    return;
                } else {
                    this.adpageNo++;
                }
                this.getOrderListFun();
            },
            loadMore2() {
                if (this.adpageNo2 == this.totalPages2) {
                    this.textLoadMore2 = "已到最底部"
                    return;
                } else {
                    this.adpageNo2++;
                }
                this.getCodeListFun();
            },
            loadMore3() {
                if (this.adpageNo3 == this.totalPages3) {
                    this.textLoadMore3 = "已到最底部"
                    return;
                } else {
                    this.adpageNo3++;
                }
                this.getMaterialTemplateListFun();
            },
            changeOption1(data) {
                let str = "";
                for (var i = 0; i < data.length; i++) {
                    if (i == data.length - 1) {
                        str += data[i]
                    } else {

                        str += data[i] + ","
                    }
                }

                this.status = str

                this.data1 = [];
                this.initList();
            },
            changeOption2(data) {
                console.log("changeOption2：" + data)
                if (data == -99) {
                    this.orderId = '';
                }
                this.initList();
            },
            changeOption3(data) {

            },
            changeOption4(data) {
                this.isNative = data;
                this.data1 = [];
                this.initList();

            },
            changeOption5(data) {

            },
            changeOption6(data) {
                this.materialId = data;
                this.initList();
            },

            handleCancel() {
                this.value = "";
                this.initList();
            },
            nativeBol() {
                this.native = true;
            },
            routeAdNative() {

                this.native = false;
                this.$refs.adNative.beforeLoad();

            },
            routeUnAdNative() {
                this.native = false;
                this.$refs.unAdNative.beforeLoad();

            },
            getCodeGroup() {
                const url = "/common/getCodeGroupStageList.json?";
                let cascaderObj = {};
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        console.log(res);
                        let arr = res.body.data.resourceList;
                        let firstObj = {};
                        let secondObj = {};
                        for (let i = 0; i < arr.length; i++) {
                            firstObj = {
                                'value': arr[i].id,
                                'label': arr[i].name,
                                'children': []
                            }
                            for (let j = 0; j < arr[i].childList.length; j++) {
                                secondObj = {
                                    'value': arr[i].childList[j].id,
                                    'label': arr[i].childList[j].name,
                                }
                                firstObj['children'].push(secondObj);
                            }
                            this.codeGroups.push(firstObj);
                        }

                        console.log(this.codeGroups)
                    }
                })
            },
            changeGroupOption(codeGroupIds) {
                if (codeGroupIds.length === 2) {
                    this.codeGroupId = codeGroupIds[1];
                } else if (codeGroupIds.length === 1) {
                    this.codeGroupId = codeGroupIds[0];
                } else if (codeGroupIds.length === 0) {
                    this.codeGroupId = "";
                }
                this.getCodeByCodeGroup();
                this.initList();
            },
            getCodeByCodeGroup() {
                const url = `/common/getCodeByCodeGroup.json?codeGroupId=${this.codeGroupId}`;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        console.log(res);
                        this.codeByCodeGroupList = res.body.data.resourceList
                        console.log(this.codeByCodeGroupList)
                    }
                })
            },
            selectCode(data) {
                console.log(data)
                this.codeId = data;
                this.initList();
            },
            getDateSelect(data) {
                console.log(data);
                this.startDate = data[0];
                this.endDate = data[1];

            },
            sureOk() {
                this.initList();
            }


        },
        components: {
            topTitle, adNative, unadNative, adNativeUpdate, unadNativeUpdate, useMaterial
        }

}

</script>



